<template>
    <li>
        <div @click="toggle">
            {{data.title}}
        </div>
        <ul v-show="open" v-if="isFolder">
            <menu-item
            v-for="item in data.subMenu"
            :data="item">
            </menu-item>
        </ul>
    </li>
</template>

<script>
export default {
    props: {
        data: {
            type: Object,
            required: true
        }
    },
    data () {
        return {
            open: true
        }
    },
    computed: {
        isFolder () {
            return this.data.subMenu && this.data.subMenu.length
        }
    },
    methods: {
        toggle () {
            if (this.isFolder) {
                this.open = !this.open
            }
        }
    },
    mounted () {
    }
}
</script>



