const routes = [
    {
        path: '/',
        component: Home
    },
    {
        path: '/download',
        component: Download
    },
    {
        path: '/docs',
        component: Docs,
        children: [
        ]
    }
]

export default new VueRouter({
    component: Root,
    routes,
    mode: 'history',
    base: __dirname,
    linkActiveClass: 'active'
})



